import React from "react";
import Button from "./Button";
import "./ButtonGroup.css";

const buttonGroup = (props) => {
	return (
		<div className = "ButtonGroup">
			<div className = "ButtonGroupRow">
				<Button click = {props.reset} value = "C"/>
				<Button value = "+/-" click = {props.minus} />
				<Button click = {() => props.operateClick("%")} value = "%" />
				<Button value = "/" click = {() => props.operateClick("/")} operation = "true" />
			</div>
			<div className = "ButtonGroupRow">
				<Button value = "7" click = {() => props.numberClick(7)} />
				<Button value = "8" click = {() => props.numberClick(8)} />
				<Button value = "9" click = {() => props.numberClick(9)} />
				<Button value = "*" click = {() => props.operateClick("*")} operation = "true" />
			</div>
			<div className = "ButtonGroupRow">
				<Button value = "4" click = {() => props.numberClick(4)} />
				<Button value = "5" click = {() => props.numberClick(5)} />
				<Button value = "6" click = {() => props.numberClick(6)} />
				<Button value = "-" click = {() => props.operateClick("-")} operation = "true" />
			</div>
			<div className = "ButtonGroupRow">
				<Button value = "1" click = {() => props.numberClick(1)} />
				<Button value = "2" click = {() => props.numberClick(2)} />
				<Button value = "3" click = {() => props.numberClick(3)} />
				<Button value = "+" click = {() => props.operateClick("+")} operation = "true" />
			</div>
			<div className = "ButtonGroupRow">
				<Button width = "48%" value = "0" click = {() => props.numberClick(0)} />
				<Button value = "." click = {props.point} />
				<Button value = "=" click = {props.showResult} operation = "true" />
			</div>
		</div>
	);
}

export default buttonGroup;