import React from "react";
import "./Button.css"

const button = (props) => {
	return (
		<button className = "Button" 
		style = {{width: props.width ? "49%" : "24%", 
		backgroundColor: props.operation ? "#dadce0" : "#f1f3f4"}}
		onClick = {props.click}>
			{props.value}
		</button>
	); 
}

export default button;