import React, {Component} from "react";
import "./Calculator.css";
import Output from "./Calculator/Output";
import ButtonGroup from "./Calculator/ButtonGroup/ButtonGroup";

class Calculator extends Component {
	state = {
		result: 0,
		prevResult: 0,
		operation: null
	};

	NumberClickHandler = (number) => {
		let a = "" + this.state.result;
		a = a.split(".");
		if(a.length === 1) {
			this.setState(prevState => ({
				result: Number(prevState.result + "" + number)
			}));	
		}
		else {
			this.setState(prevState => ({
				result: prevState.result + number
			}));	
		}
	}

	OperateHandler = (type) => {
		this.setState(prevState => ({
			operation: type,
			prevResult: prevState.result,
			result: 0
		}));	
	}

	ShowResultHandler = () => {
		if(this.state.operation === "+") {
			this.setState(prevState => ({
				result: (Number(prevState.prevResult) + Number(prevState.result)),
				operation: null
			}));
		}
		else if(this.state.operation === "-") {
			this.setState(prevState => ({
				result: (Number(prevState.prevResult) - Number(prevState.result)),
				operation: null
			}));
		}
		else if(this.state.operation === "*") {
			this.setState(prevState => ({
				result: (Number(prevState.prevResult) * Number(prevState.result)),
				operation: null
			}));
		}
		else if(this.state.operation === "/") {
			this.setState(prevState => ({
				result: (Number(prevState.prevResult) / Number(prevState.result)),
				operation: null
			}));
		}
		else if(this.state.operation === "%") {
			this.setState(prevState => ({
				result: (Number(prevState.prevResult) / 100) * Number(prevState.result),
				operation: null
			}));
		}
	}

	ChangeToMinusHandler = () => {
		this.setState(prevState => ({
			result: -1 * prevState.result
		}));
	}

	AddPointHandler = () => {
		let a = "" + this.state.result;
		a = a.split(".");
		if(a.length === 1) {
			this.setState(prevState => ({
			result: prevState.result + "."
			}));
		}
	}

	Reset = () => {
		this.setState({
			result: 0,
			prevResult: 0,
			operation: null
		});
	}

	componentDidUpdate(){
		console.log(this.state);
	}

	render() {
		return (
			<div className = "Calculator_Wrapper">
				<div className = "Calculator">
					<Output result = {this.state.result} />
					<ButtonGroup 
					numberClick = {this.NumberClickHandler} 
					operateClick = {this.OperateHandler}
					showResult = {this.ShowResultHandler}
					minus = {this.ChangeToMinusHandler}
					point = {this.AddPointHandler}
					reset = {this.Reset} />
				</div>
			</div>
		);
	}
}

export default Calculator;